<template>
  <div class="cards page-back mx-auto">
    <h2 class="bold-x-lg my-5" style="font-family: KB_C3">Î™©Ìëú ÏÑ§Ï†ï</h2>
    <div class="d-flex justify-content-end mr-5">
      <button class="btn-green target-btn" @click="openModal">Î™©Ìëú Îì±Î°ùÌïòÍ∏∞</button>
    </div>
    <!-- ÌòÑÏû¨ ÏßÑÌñâÏ§ëÏù∏ ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú -->
    <div class="mileage-goals-container">
      <h3 class="mileage-goals-title">ÌòÑÏû¨ ÏßÑÌñâÏ§ëÏù∏ ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú</h3>
      
      <div v-if="currentTargets.length > 0" class="goals-list">
        <div v-for="(target, index) in currentTargets" :key="index" class="goal-card">
          <div class="goal-info">
            <span class="goal-date">üìÖ {{ target.start_date }} ~ {{ target.end_date }}</span>
            <span class="goal-mileage">üéØ {{ target.target_mileage }} ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú</span>
            <span class="goal-rate">‚ú® {{ target.targetRate }}% Îã¨ÏÑ±</span>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: target.targetRate + '%' }"></div>
          </div>
        </div>
      </div>
      
      <div v-else class="no-goals">
        <p class="lg2">ÌòÑÏû¨ ÏßÑÌñâÏ§ëÏù∏ Î™©ÌëúÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
      </div>
    </div>
    
    <!-- ÏòàÏ†ïÎêú ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú ÎÇ¥Ïó≠ -->
    <div style="width:90%;" class="mx-auto mt-5">
      <h3 class="lg p-3 ml-5" style="text-align: left; font-family: 'KB_S4'; font-size: 18pt;">ÏòàÏ†ïÎêú ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú ÎÇ¥Ïó≠</h3>
      <div v-for="detail in futureTargets" :key="detail.target_no" class="cards card-red mx-auto m-3 pointer" style="width: 90%; height: 6vh;">
        <div class="d-flex justify-content-evenly pr-3 pl-3">
          <span class=" target-list">Î™©ÌëúÍ∏∞Í∞Ñ: {{ detail.start_date }} ~ {{ detail.end_date }}</span>
          <span class=" target-list">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ: {{ detail.target_mileage }}</span>
        </div>
      </div>
    </div>

    <!-- ÏßÄÎÇú ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú ÎÇ¥Ïó≠ -->
    <div style="width:90%;" class="mx-auto mt-5">
      <h3 class="lg p-3 ml-5" style="text-align: left; font-family: 'KB_S4'; font-size: 18pt;">ÏßÄÎÇú ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú ÎÇ¥Ïó≠</h3>
      <div v-for="detail in pastTargets" :key="detail.target_no" class="cards card-gray mx-auto m-3 pointer" style="width: 90%; height: 6vh;">
        <div class="d-flex justify-content-between pr-3 pl-3">
          <span class=" target-list">Î™©ÌëúÍ∏∞Í∞Ñ: {{ detail.start_date }} ~ {{ detail.end_date }}</span>
          <span class=" target-list">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ: {{ detail.target_mileage }}</span>
          <span class=" target-list">Îã¨ÏÑ±Î•†: {{ detail.targetRate }}%</span>
        </div>
      </div>
    </div>
  </div>

  <!-- Î™®Îã¨ -->
  <div v-if="isModalOpen" class="modal-overlay">
    <div class="modal-content">
      <div class="modal-header">
        <h2 class="modal-title">{{ mile_name }} Î™©Ìëú Îì±Î°ùÌïòÍ∏∞</h2>
        <button class="close-button" @click="closeModal">&times;</button>
      </div>
      <div class="modal-body">
        <div class="form-group">
          <label>Î™©Ìëú Í∏∞Í∞Ñ</label>
          <div class="date-range">
            <Datepicker v-model="startDate" :format="formatDate" placeholder="ÏãúÏûëÏùº"></Datepicker>
            <span class="date-separator">~</span>
            <Datepicker v-model="endDate" :format="formatDate" placeholder="Ï¢ÖÎ£åÏùº"></Datepicker>
          </div>
        </div>
        <div class="form-group">
          <label for="mileage-score">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ Ï†êÏàò</label>
          <input type="number" id="mileage-score" v-model="targetScore" />
        </div>
      </div>
      <div class="modal-footer">
        <button class="submit-button" @click="addAction">Îì±Î°ù</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  name: 'TargetMileageAdminView',
  components: {
    Datepicker,
  },
  data() {
    return {
      isModalOpen: false,
      startDate: null,
      endDate: null,
      targetScore: 0,
      mile_name: '',
    }
  },
  computed :{ // Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÍ≥µÌïòÎäî Í≥≥. 
    ...mapGetters('mile', ['getMileInfo', 'getArrayMiles']),
    ...mapGetters('login', ['getLoginInfo']),
    ...mapGetters('mileage', ['getTargets']),

    targets() {
      return this.getTargets || [];
    },
    loginInfo(){
      return this.getLoginInfo;
    },
    mileInfo() {
      return this.getMileInfo;
    },
    formattedTargets(){
      if(!this.targets) return [];

      return this.targets.map(target => {
        const startDate = new Date(target.start_date);
        const endDate = new Date(target.end_date);
        
        return{
          ...target, // ...ÏùÄ Î≥µÏÇ¨Ïùò ÏùòÎØ∏ 
          targetRate: target.achievementRate,
          startDate,
          endDate
        }
      }
    );
    },
    currentTargets(){
      const currentDate =new Date();
      currentDate.setHours(0, 0, 0, 0);
      return this.formattedTargets.filter(target =>
        target.startDate.setHours(0,0,0,0) <= currentDate && currentDate <= target.endDate.setHours(0,0,0,0)
      ).sort((a, b) => new Date(a.startDate)-new Date(b.startDate)); // ÎÇ†Ïßú Ïò§Î¶ÑÏ∞®ÏàúÏúºÎ°ú Ï†ïÎ†¨
    },
    pastTargets() {
      const currentDate = new Date();
      currentDate.setHours(0, 0, 0, 0);

      return this.formattedTargets.filter(target =>
        target.endDate.setHours(0,0,0,0) < currentDate
      );
    },
    futureTargets() {
      const currentDate = new Date();
      currentDate.setHours(0, 0, 0, 0);

      return this.formattedTargets.filter(target =>
        target.startDate.setHours(0,0,0,0) > currentDate
      ).sort((a, b) => new Date(a.startDate)-new Date(b.startDate)); 
    }
  },
  methods: {
  ...mapActions('mile', ['fetchMileInfo']),
  ...mapActions('mileage', ['addTarget', 'fetchMileTarget']),
    async addAction() {
      const targetInfo = {
        mile_no: this.loginInfo.mile_no,
        user_no: this.loginInfo.user_no,
        start_date: this.formatDate(this.startDate),
        end_date: this.formatDate(this.endDate),
        target_mileage: this.targetScore,
      }

      const response = await this.addTarget(targetInfo);

      if(response && response.data.success){
        this.showAlert('Î™©ÌëúÍ∞Ä Îì±Î°ùÎêòÏóàÏäµÎãàÎã§', 'success', '#');
      }else{
        this.showAlert('Î™©Ìëú Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'fail', '#');
      }
    },
    openModal() {
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
    showAlert(t, i, r) {
      this.$swal({
        title: t,
        icon: i,
        scrollbarPadding: false
      }).then((result) => {
        if(result.isConfirmed){
          if(r == '#'){
            location.reload(); // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®
          }else{
            this.$router.push(r);
          }
        }
      })
    },
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
  },
  async created(){
    const user_no = this.loginInfo ? this.loginInfo.user_no : null;
    if(user_no){
      await this.fetchMileInfo(user_no);
      const mileInfo = this.getMileInfo;
      if(mileInfo){
        this.mile_name = mileInfo.mile_no;
      }else{
        console.error('ÎßàÏùºÎ¶¨ÏßÄ Ïù¥Î¶ÑÏùÑ Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.');
      }
    }else{
      console.error('user_noÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }

    const mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    if (mile_no) {
      try {
        await this.fetchMileTarget(mile_no);
      } catch (error) {
        console.error('ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú Î¶¨Ïä§Ìä∏Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:', error);
      }
    } else {
      console.error('mile_noÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }
  },
 
};
</script>

